<script>
import counter from 'vue3-autocounter';

export default {
  components: {
    counter,
  },
  data: () => ({
    experienceTitle: 'Years of experience',
    githubTitle: 'Languages Utilized',
    feedbackTitle: 'Positive feedback',
    projectsTitle: 'Projects completed',
  }),
  methods: {
    getEndAmount(title) {
      const amounts = {
        'Years of experience': 4,
        'Languages Utilized': 17,
        'Positive feedback': 92,
        'Projects completed': 23,
      };
      return amounts[title];
    },
    getSuffix(title) {
      return title === 'Positive feedback' ? '%' : '';
    },
    onFinished() {
      alert('Counting finished!');
    },
  },
};
</script>


<template>
  <div class="mt-10 sm:mt-20 bg-primary-light dark:bg-primary-dark shadow-sm">
    <!-- About me counters -->
    <div class="font-general-regular container mx-auto py-20 block sm:flex sm:justify-between sm:items-center">
      <!-- Counter components -->
      <div v-for="title in [experienceTitle, githubTitle, feedbackTitle, projectsTitle]" :key="title" class="mb-20 sm:mb-0">
        <counter
          ref="counter"
          :startAmount="0"
          :endAmount="getEndAmount(title)"
          :duration="1"
          :suffix="getSuffix(title)"
          :autoinit="true"
          @finished="onFinished"
          class="font-general-medium text-4xl font-bold text-secondary-dark dark:text-secondary-light mb-2"
          aria-label="About Status Counter"
        />
        <span class="block text-md text-ternary-dark dark:text-ternary-light">
          {{ title }}
        </span>
      </div>
    </div>
  </div>
</template>
